<template>
	<div id= "add-user">
		 <div class="el-breadcrumb">
		   <span class="el-breadcrumb__item">
		     <span class="el-breadcrumb__item__inner"></span>
		     <span class="el-breadcrumb__separator">/</span>
		   </span> 
		   <span class="el-breadcrumb__item">
		     <span class="el-breadcrumb__item__inner"></span>
		     <span class="el-breadcrumb__separator">/</span>
		   </span>
		 </div>
<!-- 		<div class="form-inline margin-top-t">
			  <div class="form-group">
			    <label></label>
			    <input type="text" class="form-control" placeholder="">
			  </div>
			  <div class="form-group">
			    <label></label>
			    <input type="text" class="form-control" placeholder="">
			  </div>
			   <div class="form-group">
			    <label></label>
			    <input type="text" class="form-control" placeholder="">
			  </div>
			  <el-button type="primary"></el-button>
			  <el-button></el-button>
		</div> -->
		<div class="row margin-top-t">
			<div class="col-md-12">
				<el-button type="primary" size="small" @click="addUser"></el-button>
	<!-- 			<el-button type="primary" size="small"></el-button>
				<el-button type="danger" size="small"></el-button>
				<el-button type="danger" size="small"></el-button> -->
			</div>
		</div>

		<!---->
		<table class="table margin-top-t table-bordered">
			<thead>
				<tr class="th">
					<!-- <th><el-checkbox></el-checkbox></th> -->
					<th></th>
					<th></th>
					<th></th>
			<!-- 		<th></th>
					<th></th>
					<th></th>
					<th></th> -->
					<th></th>
					<th style="width:25%"></th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="item in listResult">
					<!-- <td><el-checkbox></el-checkbox></td> -->
					<td>{{item.name}}</td>
					<td>{{item.private_ | userType}}</td>
					<td>{{item.password}}</td>
					<td>{{item.createTime}}</td>
					<td>
					  <!-- <el-button type="primary" size="small"></el-button> -->	
		<!-- 			  <el-button type="primary" size="small"></el-button>
					  <el-button type="danger" size="small"></el-button> -->
					  <el-button type="danger" size="small" @click="delUser(item)"></el-button>
				    </td>
				</tr>
			</tbody>
		</table>
<!-- 		<div class="page-cli">
		  <el-pagination ref ="pageN" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum" :page-size="100" layout="total, prev, pager, next" :total="1000"></el-pagination>
        </div> -->

        <!---->
		<el-dialog title="" ref="add" size="small">
			  <div class="form-horizontal">
				  <div class="form-group">
				    <label class="col-sm-3 control-label"></label>
				    <div class="col-sm-8">
				      <input type="text" class="form-control" placeholder="">
				    </div>
				  </div>
				  <div class="form-group">
				    <label class="col-sm-3 control-label"></label>
				    <div class="col-sm-8">
				      <input type="text" class="form-control" placeholder="">
				    </div>
				  </div>
				  <div class="form-group">
				    <label class="col-sm-3 control-label"></label>
				    <div class="col-sm-8">
				      <input type="text" class="form-control" placeholder="">
				    </div>
				  </div>
				  <div class="form-group">
				    <label class="col-sm-3 control-label"></label>
				    <div class="col-sm-8">
				      <input type="text" class="form-control" placeholder="">
				    </div>
				  </div>
				  <div class="form-group">
				    <label class="col-sm-3 control-label"></label>
				    <div class="col-sm-8">
				      <input type="text" class="form-control" placeholder="">
				    </div>
				  </div>
				 <div class="form-group">
				    <label class="col-sm-3 control-label"></label>
				    <div class="col-sm-8">
				      <el-select v-model="value" placeholder="">
					    <el-option
					      v-for="item in options"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					  </el-select>
				    </div>
				  </div>
		      </div>
			  <span slot="footer" class="dialog-footer">
			    <el-button> </el-button>
			    <el-button type="primary"> </el-button>
			  </span>
		</el-dialog>
		<!---->
		<el-dialog title="" ref="addUser" size="small">
			  <div class="form-horizontal">
				  <div class="form-group">
				    <label class="col-sm-3 control-label"></label>
				    <div class="col-sm-8">
				      <input type="text" class="form-control" v-model="userParam.username" placeholder="">
				    </div>
				  </div>
				  <div class="form-group">
				    <label class="col-sm-3 control-label"></label>
				    <div class="col-sm-8">
				      <input type="text" class="form-control" v-model="userParam.realname"  placeholder="">
				    </div>
				  </div>
				  <div class="form-group">
				    <label class="col-sm-3 control-label"></label>
				    <div class="col-sm-8">
				      <input type="text" class="form-control" v-model="userParam.password" placeholder="">
				    </div>
				  </div>
				  </div>
			    <span slot="footer" class="dialog-footer">
			      <el-button type="primary" @click="saveUser()"> </el-button>
				  <el-button @click="closeModelUser()"> </el-button>
			  </span>
			</el-dialog>
	</div>
</template>
<script>
    import {apiService,userInfo} from "@/api"
	export default {
		data(){
			return {
				pageNum: 1,
				options: [],
				value:"",
				userTpes: [{value: 1, label: ''}, {value: 2, label: ''}],
				userParam: {},
				listResult: []
			}
		},
		methods: {
			handleSizeChange(val){

			},
			handleCurrentChange(val){

			},
			addUser(){
				var vm = this;
				vm.$refs.addUser.open();
			},
			closeModelUser(){
				var vm = this;
				vm.$refs.addUser.close();
			},
			saveUser(){
				var vm= this;
				if(vm.$myIsEmpty(vm.userParam.username)){
					vm.$myAlert.show('', 'error');
					return;
				}
				if(vm.$myIsEmpty(vm.userParam.realname)){
					vm.$myAlert.show('', 'error');
					return;
				}
				if(vm.$myIsEmpty(vm.userParam.password)){
					vm.$myAlert.show('', 'error');
					return;
				}

				apiService.requestGet('admin/addUser', vm.userParam).then(function(res){
					vm.$myAlert.show('', 'success');
					vm.userParam={};
					vm.getUsers();
					vm.$refs.addUser.close();
				},function(res){
					vm.$myAlert.show('', 'error');
				});
			},
			getUsers(){
				var vm = this;
				apiService.requestGet('admin/getAllUser').then(function(res){
					vm.listResult = res.data;

				},function(res){

				})
			},
			delUser(obj) {
				var vm = this;
				apiService.requestGet('admin/deleteUser', {userid: obj.id}).then(function(res){
					vm.$myAlert.show('', 'success');
					vm.getUsers();

				},function(res){
					vm.$myAlert.show('', 'error');
				})
			}
		},
		created() {
			var vm = this;
			vm.getUsers();
		},
		filters:{
			userType(val){
				return {0: '', 1: ''}[val]
			}
		}
	}
</script>
<style lang="scss">
	#add-user{
		.el-select{
			width: 100%;
		}
	}
</style>


